import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        sendFlag: false,
        sn: ''
    },
    mutations: {
        setDialogFlag(state, val) {
            state.sendFlag = val;
        },
        setSn(state, val) {
            state.sn = val;
        },
    },
    actions: {},
    modules: {}
})